import styled from 'styled-components';
import colors from '../../../../config/colors';
import dimensions from '../../../../config/dimensions';
import durations from '../../../../config/durations';
import { fontSizes } from '../../../../config/sizes';

export default styled.nav`
  display: flex;
  margin-bottom: 1.5rem;

  > a {
    display: inline-block;
    padding: ${dimensions.nav.padding};
    min-height: ${dimensions.button.minHeight.md};
    color: ${colors.shipCove};
    font-size: ${fontSizes.md};
    font-weight: 500;
    text-decoration: none;
    transition: all ${durations.button.transition} ease-in-out;
    cursor: pointer;
    background-color: rgba(23, 97, 253, 0.1);
    -webkit-box-shadow: 0px 0px 0px 2.25px rgba(23, 97, 253, 0.5);
    box-shadow: 0px 0px 0px 2.25px rgba(23, 97, 253, 0.5);

    :hover:not(.active) {
      background: rgba(255, 255, 255, 0.1);
    }

    &.active {
      color: ${colors.white};
      cursor: not-allowed;
      background: linear-gradient(14deg, #1761fd 0%, rgba(23, 97, 253, 0.6));
      box-shadow: 0px 0px 0px 2.25px rgba(23, 97, 253, 0.5);
    }

    :not(:last-child) {
      margin-right: 0.75rem;
    }

    &.disabled {
      cursor: not-allowed;

      :hover {
        background: transparent;
      }
    }
  }
`;
