import styled from 'styled-components';

import colors from '../../../config/colors';
import dimensions from '../../../config/dimensions';
import { fontSizes } from '../../../config/sizes';

export const StatisticStyled = styled.div`
  display: inline-block;
  width: 25%;
  > div {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: ${colors.white};
    box-shadow: 0px 2px 4px rgba(31, 30, 47, 0.1);
    border-radius: ${dimensions.defaults.radius.medium};
    padding: ${dimensions.statistic.paddingComplete};
    text-align: center;

    > header {
      color: ${colors.rihno};
      font-size: ${fontSizes.sm};
    }

    > main {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: ${fontSizes.xxl};
      padding-bottom: 10px;

      > span {
        display: inline-block;
        color: ${colors.rihno};
        font-size: 40px;

        :nth-child(1) {
          font-weight: 600;
        }

        :nth-child(2) {
          padding-left: 0.45rem;
          color: ${colors.gray};
          text-transform: lowercase;
        }
      }

      > div {
        width: 5.5rem;

        input {
          background: ${colors.white};
        }
      }
    }

    > .progress {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 0.5rem;
      background: ${colors.progressBar};
      border-radius: ${dimensions.defaults.radius.large} ${dimensions.defaults.radius.large} 0 0;
      display: flex;

      ::after {
        content: '';
        width: ${getProgressWidth};
        height: 100%;
        background: ${colors.primary};
        border-radius: ${dimensions.defaults.radius.large} ${dimensions.defaults.radius.large} 0 0;
      }
    }
  }
`;

export const IconWrapper = styled.label`
  display: flex;
  width: fit-content;
  margin: 0 auto;
  width: 50px;
  align-items: center;
  height: 50px;
  justify-content: center;
  background-color: rgb(255 184 34 / 11%);
  color: #ffb822 !important;
  background: rgba(255, 184, 34, 0.08);
  border-radius: 100px;
  padding: 35px;
  margin-bottom: 30px;
`;

function getProgressWidth({ value, total }) {
  return `${Math.floor((value / total) * 100)}%`;
}
